import { useLocation, useNavigate } from 'react-router-dom';
import 'assets/css/rentalHistoryDetail.css';

function Detail() {
  const navigate = useNavigate();
  const location = useLocation();
  const row = location.state;
  const historyBack = () => {
    navigate(-1);
  };

  return (
    <div className="section__grid__wrap content rental__history__detail">
      {/* sub_nav */}
      <div className="sub__nav">
        <div className="left">
          <button onClick={historyBack}>
            <svg viewBox="0 0 24 24" stroke="white">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M11 17l-5-5m0 0l5-5m-5 5h12"
              />
            </svg>
          </button>
          <dl>
            <dt className="f__medium">QR인증 내역</dt>
          </dl>
        </div>
      </div>
      {/* // sub_nav */}
      {/* 디바이스 ID */}
      <section className="id__wrap">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">디바이스ID</p>
          </div>
          <input defaultValue={row.deviceName} className="custom__input" readOnly />
        </div>
      </section>
      {/* // 디바이스 ID */}
      {/* Transaction ID */}
      <section className="id__wrap">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">Transaction ID</p>
          </div>
          <input type="text" defaultValue={row.transaction} className="custom__input" readOnly />
        </div>
      </section>
      {/* // Transaction ID */}
      {/* 디바이스명 */}
      <section className="device__name grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">디바이스명</p>
          </div>
          <input type="text" defaultValue="을지로3가" className="custom__input" />
        </div>
      </section>
      {/* // 디바이스명 */}
      {/* 스테이션 */}
      <section className="user__certification station grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">스테이션</p>
          </div>
          <div className="list__wrap">
            <ul>
              <li>
                <dl>
                  <dt>QR생성</dt>
                  <dd>
                    <input
                      type="text"
                      className="custom__input"
                      defaultValue="2022-12-01 11:00:11"
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>OTP</dt>
                  <dd>
                    <input type="number" className="custom__input" defaultValue="50" />
                  </dd>
                </dl>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 스테이션 */}
      {/* 사용자앱 */}
      <section className="user__certification grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">사용자앱</p>
          </div>
          <div className="list__wrap">
            <ul>
              <li>
                <dl>
                  <dt>QR스캔</dt>
                  <dd>
                    <input
                      type="text"
                      className="custom__input"
                      defaultValue={row.certificationDate}
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>Platform</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue={row.platform} />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>App 버전</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue={row.appVersion} />
                  </dd>
                </dl>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 사용자앱 */}
      {/* 오류정보 */}
      <section className="error__info grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">오류정보</p>
          </div>
          <div className="list__wrap">
            <ul>
              <li>
                <dl>
                  <dt>발생일시</dt>
                  <dd>
                    <input
                      type="text"
                      className="custom__input"
                      defaultValue="2022-12-01 11:00:11"
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>오류명</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue="도어" />
                  </dd>
                </dl>
              </li>
            </ul>
            <dl>
              <dt>오류메시지</dt>
              <dd>
                <input type="text" className="custom__input" defaultValue="도 닫기 오류" />
              </dd>
            </dl>
          </div>
        </div>
      </section>
      {/* // 오류정보 */}
    </div>
  );
}

export default Detail;
